export default {"name":"五盖山米茶","img":"\\lvcha\\wugaisanmicha\\img_20191122100156od3h1j.jpg","desc":["五盖山米茶产于湖南省郴县的五盖山。在明代就被列为贡品，当时米茶系清明时采摘“未开苞”的芽叶制成。此茶叶外形芽头紧秀重实，米粒大小。据说一筒茶有一筒米重，“米茶”之名，由此而来。茶芽茸毛特多，色泽银光隐翠;内质香气清鲜高雅，滋味清甜，毫味重;汤色谷黄嫩绿明净，叶底翠绿匀齐。"],"menus":[{"name":"五盖山米茶茶叶历史","content":[{"text":"五盖山米茶\n        ，产于湖南省郴县的五盖山。据明《万历郴州志》记载：“五盖山在州东南三十里，高二十里，周围百八十里，山多云雾”。该山为岭南山脉之一，有五个山峰耸立如盖，同时常年时有“云、雾、雨、露、雪”所盖，故称五盖山。其主峰碧云峰，海拔1620米，有郴阳第一峰之称。全山总面积20余万亩，广布野生茶树。 山势雄伟，绿林苍翠，涧流淙淙，冬不严寒，夏不酷暑，土质肥沃，为花岗岩母质发育的灰砂土，堪为种茶之佳境。人称：“五盖山七十二峰，峰峰有宝;一峰无宝，也有黄连甘草”，可见这里资源之丰富。这里产茶，品质优异，据清嘉庆《郴县县志》记载：“茶，郴属均产，以五盖为佳也”。茶叶品质，尤为位于海拔1000至1400米的碧云庵、万惠庵、两江口一带所产为冠。"}]},{"name":"五盖山米茶茶叶特点","content":[{"text":"五盖山米茶\n        外形芽头紧秀重实，米粒大小。据说一筒茶有一筒米重，“米茶”之名，由此而来。茶芽茸毛特多，色泽银光隐翠;内质香气清鲜高雅，滋味清甜，毫味重;汤色谷黄嫩绿明净，叶底翠绿匀齐。"},{"src":"\\lvcha\\wugaisanmicha\\img_20191122100156bo99ut.jpg"}]},{"name":"五盖山米茶加工工艺","content":[{"text":"晒青"},{"text":"包括室外晒青(使茶青萎蔫)和晒青后茶青移入茶厂内凉青(使茶青得到初步返活)两个阶段。晒青时间早晨茶青、春季茶青时间较长。夏、暑、秋茶青，下午茶青晒青时间较短。晒青的具体做法：进厂茶青先摊放在竹笪上散发叶温，厚度为10厘米左右。然后将摊放在竹笪或竹筛上的茶青置于室外晒坪上晒青，厚度以3厘米为宜。晒青过程中翻拌3—4次，一般情况下，当室温达到26—28℃时，则室外晒青20—30分钟即可，晒青适度的标准是：以顶叶下数第二叶为准，叶片由原来的青绿有光晒至暗绿无光，并由原来的紧张状态变成柔软，手摸有柔滑感便可收青，摊放在室内阴凉处凉青。"},{"src":"\\lvcha\\wugaisanmicha\\img_20191122100156chxnzf.jpg"},{"text":"做青"},{"text":"又称摇青或碰青，包括摇青或碰青和静置两个交错过程，是产生色、香、味的关键工序。做青时室内的温度以22—26℃为宜，相对湿度75%—85%最为理想，所以一般单枞茶做青均在当天傍晚7点至第二天上午6点。摇青次数为6—7次，历时12—14小时。 每次摇青后，茶青必须摊放在竹制的直径为105厘米的簸箕土，静置一段时间，如此交替进行。第一、二、三次用竹制的直径为130厘米，深为28厘米，网眼为0·5厘米×0·5厘米的摇筛，中横一梁，绑挂在茶青房的梁上，每筛投叶5—6公斤，两人对面手执筛沿作上下摇动，使片在筛内作波浪式滚动。第四至六次用竹制的长200厘米、口径为75厘米、网眼为0·6厘米×0·6厘米的摇筛，每分钟转35转左右，投叶量为25—35公斤，在长筒形摇笼中摇青，每次摇青后，叶片返活，气味增长(第一至三次静置时为水青气，第4—5次做青后静置时为青辣气，第六次做青后静置时为花果甜味略带青气)，静置后叶片气味将消失时，应进行第2次摇青。每次摇青后静置时气味嗅之以清纯为优，将近杀青时以清甜微青或蜜香带青味为好。做青适度标准：茶青香气显著，叶片绿红比例恰当，绿的面积占70%，红的面积占30%，参考做青工序：第一次摇筛(来回十五次)，静置2小时。第二次摇筛(来回三十次)静置2小时，第三次摇筛(来回四十五次)，静置2小时。第四次摇笼(5分钟)，静置2·5小时，第五次摇笼(10分钟)，静置2·5小时，第六次摇笼(20分钟)静置1—2小时。制作工序杀青选用电动滚筒杀青机，每次投茶青量以15—25公斤、炒5—8分钟下机为宜，杀青适度的标准是：叶片皱卷，叶色绿明，手捏茶胚无水分，干松不粘手，茶梗折而不断，闻之有清香。"},{"text":"揉捻"},{"text":"采用35型或40型揉捻机，热揉(下杀青机后即可上机揉捻)，揉捻时间以7—10分钟为宜，揉捻适度标准：细胞破损率40%左右，条索紧而不松。"},{"text":"初烘"},{"text":"采用自动链式烘干机或手拉百叶箱式烘干机，烘温为110—120℃，时间20—30分钟，至七成干下机摊凉。"},{"text":"复焙"},{"text":"将摊凉后的茶叶置于筛温为50—60℃的焙笼上，烘培1.5—2小时，中间翻拌2—3次，茶叶摊放厚度为焙笼高度的一半。中间挖一个宽1.5厘米的小洞。焙笼上用簸箕覆盖，以防香气散失。当烘至干嗅清香、茶梗折之即断、茶叶捏之即粉碎、含水量为5%左右为适度，出焙、摊凉、包装。"}]},{"name":"五盖山米茶储藏方法","content":[{"text":"1、瓦罐储茶法"},{"text":"此法古代就有明人冯梦祯<快雪堂漫录>云：\"实茶大瓮，底置箬，封固倒放，则过夏不黄，以其气不外泄也.\"茶叶含水量不能超过6%.可用生石灰除湿.具体操作方法同石灰块保存法。"},{"text":"2、罐藏法"},{"text":"容器选用装糕点或者其他食品的金属听、箱、罐、盒，或铁或铝或纸或纸品，或方或圆或扁或不规则性.重要的是茶要干燥，袋口封好.此法简便，取同方便。"},{"text":"3、塑料袋贮茶法"},{"text":"选用密度高、高压、厚实、强度好、无异味的食品包装袋.茶叶可以事先用较柔软的净纸包好，然后置于食品袋内，封口即成。"},{"text":"4、热水瓶贮茶法"},{"text":"可用保温不佳而废弃的热水瓶，内充干燥的雀舌，盖好瓶塞，用蜡封口。"},{"text":"5、冰箱保存法"},{"text":"五盖山米茶\n        装入密度高、高压、厚实、强度好、无异味的食品包装袋，然后置于冰箱冷冻室或者冷藏室.此法保存时间长、效果好，但袋口一定封牢，封严实，否则会回潮或者串味，反而有损五盖山米茶茶叶的品质。"}]},{"name":"五盖山米茶冲泡方法","content":[{"text":"赏茶：由茶罐直接将茶倒入茶荷(一种盛茶的专用器皿，类似小碟)。由专人奉至饮者面前，以供其观看茶形，闻取茶香。"},{"src":"\\lvcha\\wugaisanmicha\\img_20191122100156dmlc34.jpg"},{"text":"温壶：将热水冲入壶中至半满即可，再将壶内的水倒出到茶池中。"},{"text":"置茶：将茶荷的茶叶拨入壶中。"},{"text":"温润泡：注水入壶到满为至，盖上壶盖后立将水倒入茶公道杯中。(目的是为茶叶吸收水份并可洗去茶的不洁之嫌。)温杯烫盏：将公道杯中的水再倒入茶盅中，以提高杯的温度，有利于更好的泡制茶叶。"},{"text":"第一泡：将适温的热水冲入壶中，注意时间以所泡茶叶的品质而定。"},{"text":"干壶：执起茶壶，先将壶底部在茶巾上沾一下，拭去壶底的水滴。 倒茶：将茶汤倒入公道杯中。"},{"text":"分茶：将公道杯的茶汤倒入茶杯中，以七分满为宜。"},{"text":"洗壶、去渣：先将壶中的残茶取出，再冲八水将剩余茶渣清出倒入池中。"},{"text":"倒水：将茶池中的水倒掉。清洗一切用具，以备再用。"}]},{"name":"五盖山米茶功效作用","content":[{"text":"1、美容护肤，抗衰老。"},{"text":"五盖山米茶\n        所含的抗氧化剂有助于抵抗老化。因为人体新陈代谢的过程，如果过氧化，会产生大量自由基，容易老化，也会使细胞受伤。SOD(超氧化物歧化)是自由基清除剂，能有效清除过剩自由基，阻止自由基对人体的损伤。五盖山米茶中的儿茶素能显著提高SOD的活性，清除自由基。五盖山米茶所含有的茶多酚，能够起到清除面部多余的油脂，收敛毛孔的作用。"},{"text":"2、塑身减脂。"},{"text":"五盖山米茶含有茶碱及咖啡因，可以经由许多作用活化蛋白质激酶及三酸甘油酯解脂酶，减少脂肪细胞堆积，因此达到减肥功效。也就是茶叶中的咖啡碱能提高胃液的分泌量，可以帮助消化，增强分解脂肪的能力。"},{"text":"3、防辐射。"},{"text":"茶多酚及其氧化产物具有吸收放射性物质锶90和钴60毒害的能力。现在的白领阶层经常使用电脑，免不了会受到辐射的侵害。"},{"text":"4、提神醒脑。"},{"text":"茶叶中的咖啡碱能促使人体中枢神经兴奋，增强大脑皮层的兴奋过程，起到提神益思、清心的效果。所以建议大家在白天疲劳的时候也可以喝\n        绿茶\n        提神。"},{"text":"5、利尿解乏。"},{"text":"茶叶中的咖啡碱可刺激肾脏，促使尿液迅速排出体外，提高肾脏的滤出率，减少有害物质在肾脏中滞留时间。咖啡碱还可排除尿液中的过量乳酸，有助于使人体尽快消除疲劳。"},{"text":"6、抵抗病毒菌。"},{"text":"茶多酚有较强的收敛作用，对病原菌、病毒有明显的抑制和杀灭作用，对消炎止泻有明显效果。对急性和慢性痢疾也有治愈作用。"}]},{"name":"五盖山米茶历史故事","content":[{"text":"五盖山米茶\n        ，产于湖南郴州的五盖山。该山为岭南山脉之一，山峰耸立，直插云霄，峰顶常年被云雾、雨、露、雪所盖，故称五盖山。五盖山山势雄伟，绿林苍翠，涧流淙淙，冬无严寒，夏无酷暑，土质肥沃，为花岗岩母质发育的灰砂土，堪为种茶之佳境。人称“五盖山七十二峰，峰峰有宝;一峰无宝，也有黄连甘草”，可见这里资源之丰富。 五盖山茶早在明代已被列为贡品。当时多系清明时采摘“末开苞”的嫩芽制成。相传所制茶叶“一升茶有一升米重”;冲泡时，杯中热气，初时犹如一朵白云盖碗，然后呈线状袅绕二尺余散去;杯中茶芽芽尖朝上，柄端朝下，有几起几落，饮之“茶味清洌，颊齿留香”，但那些“体弱未惯饮者，只可半盅;多则顷即昏眩，汗出涔涔”，此茶性烈，由此可见。"},{"text":"米茶冲泡后，杯中热气初时犹如一朵白云盖碗，然后变为线状袅绕上升至二尺余才散去;杯中茶芽芽尖朝上，柄端朝下，几起几落，品饮后觉得“茶味清冽，颊齿留香”,饮后余香生津。。由于米茶特别珍贵，当地人们连祭祀庙里的菩萨之前，也要先饮上好的米茶作为洗涤肠胃，以表对菩萨之敬意，祈祷菩萨保佑自己。"},{"text":"五盖山不仅茶叶好，泉水亦好，郴州南7.5公里的园泉，水味甘醇，水质清洁，水色晶莹，水温冬夏相近，用以沏茶，香气清洌，滋味鲜甘，唐代诗人刘禹锡盛赞饮五盖山米茶后“悠扬喷鼻宿醒散，消峭彻骨烦襟开”。"},{"text":"说明饮这种香气扑鼻的茶叶，头脑即刻清醒，胸中的烦躁也一扫而光了，面对不少名士，都曾慕名前往郴州，取水煎茶，饮之为快。"},{"text":"唐代陆羽曾品评了天下各地名泉共有20处，五盖山郴州南7.5千米处的园泉，被陆羽称为“天下第十八泉”。该泉水质清洁，水色晶莹，水温冬夏无异，恒温18℃左右，用以沏茶，香气格外清纯，滋味尤为甘美，古时历代名士过往郴州，都慕名前往，以一饮米茶、园泉水为快。宋代留下的“天下第十八泉”石刻，至今还完好地保存着，成为郴县八景之一。"},{"text":"五盖山米茶制造方法业已失传。1980年以来，经湖南省有关单位组织力量多次试制而成功。产品经湖南省名茶评比和有关单位鉴定，均获得高度评价，被誉为湖南名茶中的一颗明珠。"}]}]}